.app {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.logo {
  font-family:'Atiba';
  color: white;
  text-decoration: none;
  background: linear-gradient(45deg, #e3b035 30%, #FF8E53 90%);
  background-size: 300%;
  padding-right: 10px;
  animation: gradient 3s ease infinite;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.btn {
  background: linear-gradient(45deg, #e3b035 30%, #FF8E53 90%);
  color: white;
  margin: 10px;
}

.btn:active {
  animation: btn-gradient 3s ease infinite;
}

@keyframes btn-gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
  }
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
  }
}
.app-body{
  background-color: #4dc7f8;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@font-face {
  font-family: 'Atiba';
  src: local('Atiba'), url(./assets/airstrike.ttf) format('trueType');
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
